import performSearch from '../../search.js'
import { clearResults, hideResults, showResults } from '../resultList.js'
import {
    isLoading,
    getLastStableInputValue,
    setLastStableInputValue,
    wasInputCleared,
    resetInputCleared,
    setInitialInputValue,
    isBackspaceActive,
    setBackspaceState,
} from './inputState.js'
import { normalizeText } from './inputUtils.js'
import { updateButtonState } from '../../ui'
import { handleEmptyInput, handleShortInput } from './inputProcessing'

const SEARCH_DELAY = 700 // ‚è≥ Delay before executing the search
let timeoutId // üïí Timer for delaying the search

// üìå Handles text input in the search field
export function onInput(event, input, resultsContainer) {
    // üõë If a search is already in progress, prevent further input
    if (isLoading()) {
        event.preventDefault()
        input.value = getLastStableInputValue() // ‚è™ Restore the last stable value
        return
    }

    // üîÑ Clear the previous timeout to prevent excessive search triggers
    clearTimeout(timeoutId)

    // ‚úÇÔ∏è Trim whitespace from input
    const currentValue = input.value.trim()

    // üõë If the input is empty, reset the state
    if (currentValue === '') {
        handleEmptyInput(resultsContainer)
        return
    }

    // ‚ÑπÔ∏è If the input length is 3 characters or less, apply short input logic
    if (currentValue.length <= 3) {
        handleShortInput(resultsContainer)
    }

    // üìå If the input was manually cleared, reset the flag and store the initial value
    if (wasInputCleared()) {
        setInitialInputValue(currentValue)
        resetInputCleared()
    }

    // üõë If the input is effectively unchanged (ignoring case and spaces), do not trigger a search
    if (normalizeText(currentValue) === normalizeText(getLastStableInputValue())) {
        return
    }

    // üîò Update the button state (üîç Search or ‚úñ Clear)
    updateButtonState(currentValue.length > 0 ? 'clear' : 'search')

    // üîÑ Require at least 3 characters to perform a search
    if (currentValue.length < 3) return

    // ‚è≥ Start the search with a delay (`SEARCH_DELAY`)
    timeoutId = setTimeout(() => {
        // ‚úÖ Ensure no active search is in progress
        if (!isLoading()) {
            if (isBackspaceActive()) return // üõë Do not trigger search if `Backspace` is being held
            if (normalizeText(currentValue) === normalizeText(getLastStableInputValue())) return // üõë Ensure the value has actually changed

            performSearch(currentValue, resultsContainer) // üîç Execute AJAX search
            setLastStableInputValue(currentValue) // üíæ Store the last stable value
        }
    }, SEARCH_DELAY)
}

// üìå Handles text cutting (`Ctrl+X`)
export function onCut(event, input, resultsContainer) {
    // üõë If a search is in progress, prevent `cut`
    if (isLoading()) {
        event.preventDefault()
        return
    }

    // ‚è≥ After 10ms, check if the input field is now empty
    setTimeout(() => {
        if (input.value.trim() === '') {
            clearResults(resultsContainer) // üóë Clear search results
            hideResults(resultsContainer) // üîΩ Hide the results list

            // üîò Show the search button
            updateButtonState('search')
        }
    }, 10)
}

// üìå Handles click inside the input field (displays search results)
export function onClick(resultsContainer) {
    showResults(resultsContainer)
}

// üìå Handles `Backspace` key press (marks it as held)
export function onKeydown(event) {
    if (event.key === 'Backspace') {
        // üõë Mark `Backspace` as held
        setBackspaceState(true)
    }
}

// üìå Handles `Backspace` key release (allows search)
export function onKeyup(event) {
    if (event.key === 'Backspace') {
        // ‚úÖ Now `Backspace` is no longer held
        setBackspaceState(false)
    }
}
